import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Menu, Switch, Layout, Icon } from 'antd'
import { menus } from './menu';
import './index.less';
import logoImage from './images/logo.png';

const { Sider, Header } = Layout;
const { SubMenu } = Menu;

class Home extends Component {
	constructor(props) {
		super(props);

		this.trigger = this.trigger.bind(this);
		this._getMenus = this._getMenus.bind(this)

		this.state = {
			collapsed: false, //当前侧边栏收起状态
			openKeys: ['staff']
		}
	}

	trigger() {
		this.setState({
			collapsed: !this.state.collapsed
		})	
	}

	// 递归生成菜单
	_getMenus(menus) {
	    return menus.map((item) => {
	      	if (item.children) {
		        return (
		          	<SubMenu
			            key={item.url}
			            title={<span>{item.name}</span>}
			        >
		           		{this._getMenus(item.children)}
		          	</SubMenu>
		        )
	      	}
	      	return (
		        <Menu.Item key={item.url}>
		          	<Link to={item.url}>
		            	<span>{item.name}</span>
		          	</Link>
		        </Menu.Item>
	      	)
	    })
	}
  	

	onOpenChange = (openKeys) => {
	    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
	    // if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
	    //   this.setState({ openKeys });
	    // } else {
	    //   this.setState({
	    //     openKeys: latestOpenKey ? [latestOpenKey] : [],
	    //   });
	    // }
	}

	render() {
		return (
			<Layout className="container">
				<Sider
					collapsed={this.state.collapsed}
				>
					<div className="logoContainer">
						<Link to="/"><img src={logoImage} alt="会员中心" className="logo" /></Link>
          				<span className="title">会员中心</span>
					</div>
					<Menu
						theme="dark"
						mode="inline"
						defaultOpenKeys={['user', 'organization']}
						defaultSelectedKeys={['/user/rightsmanagement/one']}
						// openKeys={this.state.openKeys}
        				onOpenChange={this.onOpenChange}
					>
						{this._getMenus(menus)}
					</Menu>
				</Sider>
				<Layout>
					<Header className="header">
						<Icon
			              	className="trigger"
			              	type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
			              	onClick={this.trigger}
			            />
					</Header>
				</Layout>
			</Layout>	
		);
	}
}

export default Home;